export default {
	
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			wei: this.Static+'my/15.png',
			xuan: this.Static+'my/16.png',
			lineRightArrow: this.Static+'images/common/line-right-arrow.png',
			tou:false,
			images:0,
			imag:1,
			array:[],
			date:currentDate
		}
	},
	mounted() {
		
	},
	onLoad:function(){
		uni.setNavigationBarTitle({//标题 已收藏
			title: '公司信息'
		});
	},
	methods: {
		image(){
			this.images=1;
			this.imag=0;
		},
		ima(){
			this.images=0;
			this.imag=1;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		bindDateChange: function(e) {
			console.log(e)
			this.date = e.target.value
			console.log(this.date)
		},
		baoadd(){
			console.log(this.date)
			if(this.images==1){
				console.log('选择永久')
				this.$store.commit('setEffective','永久');
			}else if(this.imag==1){
				console.log('选择短期')
				this.$store.commit('setEffective',this.date);
			}
			uni.navigateBack({})
		},
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
	},
}
